import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    path_api: "https://www.giantiot.com:8084/api/v1/admins/dashboards/",
    path_header: null,
    loading: false,
    place: "",
    update: false,
    active: 0,
  },
  mutations: {
    setHeader(state, val) {
      state.path_header = {
        Authorization: "Bearer " + val,
      };
    },
    setloading(state, val) {
      state.loading = val;
    },
    setPlace(state, val) {
      state.place = val;
    },
    setUpdate(state, val) {
      state.update = val;
    },
    setactive(state, val) {
      state.active = val;
    },
  },
  actions: {},
  modules: {},
});
