<template>
  <v-dialog
    v-model="dialog"
    persistent
    :overlay="false"
    transition="dialog-transition"
    width="300"
    height="300"
    max-height="300"
  >
    <v-card height="127">
      <v-row class="pa-3 mx-0">
        <v-col class="d-flex justify-center align-center">
          <div>Are you sure to edit CID ?</div>
        </v-col>
      </v-row>
      <v-row class="mx-0">
        <v-divider></v-divider>
      </v-row>
      <v-row class="mx-0">
        <v-col
          ><v-btn small text block color="pluscovid" @click="dialog = false"
            >NO</v-btn
          ></v-col
        >
        <v-divider vertical></v-divider>
        <v-col
          ><v-btn small text block color="primary" @click="editCID()"
            >YES</v-btn
          ></v-col
        >
      </v-row>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ["one_id"],
  data() {
    return {
      dialog: false,
      editedone_id: "",
      newCID: "",
    };
  },
  methods: {
    openDialog(one_id, newCID) {
      this.dialog = true;
      this.editedone_id = one_id;
      this.newCID = newCID;
    },
    async editCID() {
      this.$store.commit("setloading", true);
      const body = { cid: this.newCID };
      await this.axios
        .put(this.$store.state.path_api + "users/" + this.editedone_id, body, {
          headers: this.$store.state.path_header,
        })
        .then(async () => {
          this.dialog = false;
          this.$store.commit("setUpdate", true);
          this.$emit("status");
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.confirm_dialog {
  position: fixed !important;
  left: 0;
  bottom: 0;
  width: 100%;
}
>>> .my-custom-dialog {
  align-self: flex-end;
}
</style>