/* eslint-disable prettier/prettier */
<template>
  <div>
    <v-card elevation="0">
      <v-container
        grid-list-xs
        fluid
        class="pa-0"
      >
        <apexchart
          type="bar"
          :options="options"
          :series="series"
          :height="heightchart"
        ></apexchart>
      </v-container>
    </v-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      options: {
        chart: {
          type: "bar",
        },
        dataLabels: {
          enabled: true,
        },
        series: [],
        noData: {
          text: "Loading...",
        },
      },
      series: [
        {
          name: "negative",
          data: [],
        },
        {
          name: "positive",
          data: [],
        },
      ],
      summary_list: null,
    };
  },
  methods: {
    async get_summary() {
      await this.axios
        .get(this.$store.state.path_api + "sum", {
          headers: this.$store.state.path_header,
        })
        .then((result) => {
          this.summary_list = result.data.result;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    async updateChart() {
      let gate_list = [];
      let green_list = [];
      let red_list = [];
      for (const item of this.summary_list) {
        gate_list.push(item.gate_name);
        green_list.push(item.green_case);
        red_list.push(item.red_case);
      }
      this.options = {
        title: {
          text: "Summary Information/Gate",
          align: "center",
          margin: 24,
          offsetX: 0,
          offsetY: 0,
          floating: false,
          style: {
            fontSize: "17px",
            fontWeight: "bold",
            color: "#000",
          },
        },
        plotOptions: {
          bar: {
            dataLabels: {
              position: "top",
            },
          },
        },
        dataLabels: {
          offsetY: -20,
          style: {
            fontSize: "12px",
            fontWeight: "bold",
            colors: [
              this.$vuetify.theme.themes.light.negcovid,
              this.$vuetify.theme.themes.light.pluscovid,
            ],
          },
        },
        chart: {
          id: "covid_chart",
          toolbar: {
            show: false,
          },
        },
        xaxis: {
          categories: gate_list,
        },
        colors: [
          this.$vuetify.theme.themes.light.negcovid,
          this.$vuetify.theme.themes.light.pluscovid,
        ],
      };
      this.series = [
        {
          name: "negative",
          data: green_list,
        },
        {
          name: "positive",
          data: red_list,
        },
      ];
    },
  },
  async mounted() {
    await this.get_summary();
    await this.updateChart();
  },
  computed: {
    heightchart() {
      if (this.$vuetify.breakpoint.name == "xs") {
        return 340;
      }
      return 400;
    },
  },
};
</script>

<style lang="scss" scoped></style>
