<template>
  <div>
    <v-card
      elevation="1"
      outlined
      v-bind:class="[$vuetify.breakpoint.name === 'xs' ? 'ma-1' : 'ma-10']"
    >
      <v-card-title>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
        mobile-breakpoint="0"
        :headers="headers"
        :items="user_list"
        :search="search"
        :hide-default-footer="true"
        :items-per-page="user_list.length"
      >
        <template v-slot:item.actions="{ item }">
          <v-icon small class="mr-2 align-center" @click="editItem(item)">
            mdi-pencil
          </v-icon>
        </template>

        <template v-slot:body="{ items }">
          <tbody>
            <tr v-for="item in items" :key="item.name">
              <td>
                <read-more
                  v-if="
                    $vuetify.breakpoint.name === 'xs' ||
                    $vuetify.breakpoint.name === 'sm'
                  "
                  more-str=""
                  :text="item.name"
                  :max-chars="15"
                ></read-more>
                <read-more
                  v-if="
                    $vuetify.breakpoint.name === 'md' ||
                    $vuetify.breakpoint.name === 'lg'
                  "
                  more-str=""
                  :text="item.name"
                  :max-chars="30"
                ></read-more>
              </td>
              <td>
                <div v-if="item.cid.length >= 13">
                  {{ "xxxxxxxxx" + item.cid.slice(9, 13) }}
                </div>
                <div v-if="item.cid.length < 13">
                  {{ "-" }}
                </div>
              </td>
              <td class="d-flex justify-center align-center">
                <v-icon small class="mr-2 align-center" @click="editItem(item)">
                  mdi-pencil
                </v-icon>
              </td>
            </tr>
          </tbody>
        </template>
      </v-data-table>
    </v-card>
    <v-dialog v-model="dialog" width="800" height="100" persistent>
      <v-container grid-list-xs class="pa-0" fluid>
        <v-card elevation="0">
          <v-toolbar dark color="negcovid">
            <v-toolbar-title> Edit </v-toolbar-title>
          </v-toolbar>

          <v-form ref="form">
            <v-row class="px-2 pt-5 pb-0 mb-0 mx-0">
              <v-col>
                <v-text-field
                  label="Name"
                  v-model="editedName"
                  readonly
                  solo
                  dense
                ></v-text-field>
              </v-col>
            </v-row>

            <v-row class="px-2 pb-5 pt-0 mt-n3 mx-0">
              <v-col>
                <v-text-field
                  label="CID"
                  v-model="editedCID"
                  hide-details="auto"
                  :rules="rules"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-form>

          <v-row class="mx-0">
            <v-divider></v-divider>
          </v-row>
          <v-row class="mx-0">
            <v-col
              ><v-btn text block color="pluscovid" @click="dialog = false"
                >CLOSE</v-btn
              ></v-col
            >
            <v-divider vertical inset></v-divider>
            <v-col
              ><v-btn text block color="primary" @click="EditCID()"
                >SAVE</v-btn
              ></v-col
            >
          </v-row>
        </v-card>
      </v-container>
    </v-dialog>
    <ConfirmDialog ref="confirm" v-on:status="dialog = false" />
  </div>
</template>

<script>
import ConfirmDialog from "../components/confirm_popup.vue";
export default {
  components: {
    ConfirmDialog,
  },
  data() {
    return {
      place: "",
      search: "",
      headers: [
        {
          text: "Name",
          align: "start",
          value: "name",
        },
        { text: "CID", value: "cid" },
        { text: "Actions", value: "actions", align: "center", sortable: false },
      ],
      user_list: [],
      dialog: false,
      editedName: "",
      editedCID: "",
      editedone_id: "",
      rules: [
        (v) => v.length == 13 || "the number of CID must be 13",
        (v) => /^[0-9]*$/.test(v) || "this field must be numeric",
      ],
    };
  },
  async mounted() {
    await this.get_place();
    await this.$store.commit("setPlace", this.place);
    await this.get_user_data();
    this.$store.commit("setloading", false);
  },
  methods: {
    async get_place() {
      await this.axios
        .get(this.$store.state.path_api + "stat", {
          headers: this.$store.state.path_header,
        })
        .then(async (result) => {
          this.place = result.data.result[0].place;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    async get_user_data() {
      await this.axios
        .get(this.$store.state.path_api + "users", {
          headers: this.$store.state.path_header,
        })
        .then(async (result) => {
          for (const item of result.data.result) {
            this.user_list.push({
              name: item.name,
              cid: item.cid,
              one_id: item.one_id,
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    editItem(item) {
      this.editedItem = Object.assign({}, item);
      this.editedName = this.editedItem.name;
      this.editedCID = this.editedItem.cid;
      this.editedone_id = this.editedItem.one_id;
      this.dialog = true;
    },
    EditCID() {
      if (this.$refs.form.validate()) {
        this.$refs.confirm.openDialog(this.editedone_id, this.editedCID);
      }
    },
  },
  computed: {
    update() {
      return this.$store.state.update;
    },
  },
  watch: {
    update(newValue) {
      if (newValue) {
        location.reload();
        localStorage.setItem("active", 1);
        setTimeout(() => {
          this.$store.commit("setloading", false);
          this.$store.commit("setUpdate", false);
        }, 500);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
</style>