import Vue from "vue";
import VueRouter from "vue-router";
import store from "../store";
import main from "../views/main.vue";
import main_mobile from "../views/dashboard.vue";
// import main_desktop from "../views/main_desktop.vue";
import User from "../views/user_management.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "main",
    component: main,
    children: [
      {
        path: "/",
        name: "dashboard",
        component: main_mobile,
      },
      {
        path: "/usermanagement",
        name: "usermanagement",
        component: User,
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  if (to.name) {
    store.commit("setloading", true);
    next();
  }
  if (from.name === null && to.name === "dashboard") {
    var searchParams = new URLSearchParams(window.location.search);
    const token = searchParams.get("onechat_token");
    if (token) {
      localStorage.setItem("token", token);
    }
    store.commit("setHeader", localStorage.getItem("token"));
    next();
  }
  if (to.name !== "dashboard") {
    store.commit("setHeader", localStorage.getItem("token"));
  }
  next();
});

export default router;
