<template>
  <div>
    <v-app-bar
      color="negcovid"
      dense
      dark
      shrink-on-scroll
      scroll-target="#scrolling-techniques-2"
      :height="height"
    >
      <template
        v-slot:extension
        v-if="
          $vuetify.breakpoint.name === 'lg' || $vuetify.breakpoint.name === 'md'
        "
      >
        <v-tabs v-model="active" align-with-title>
          <v-tab @click="goPage('/', 0)">Dashboard</v-tab>
          <v-tab @click="goPage('/usermanagement', 1)">User Management</v-tab>
        </v-tabs>
      </template>
      <v-toolbar-title>
        <div
          style="font-size: 25px; font-weight: bold"
          v-bind:class="[
            $vuetify.breakpoint.name === 'xs' ? xsClass : otherClass,
          ]"
        >
          {{ place }}
        </div>
      </v-toolbar-title>

      <v-spacer></v-spacer>
      <v-row class="pt-5 px-2">
        <v-col cols="12" class="pl-0 ml-0">
          <v-row class="d-flex align-center justify-end">
            <div
              v-if="$vuetify.breakpoint.name !== 'xs'"
              class="pr-5"
              style="font-size: 17px"
            >
              {{ this.profile.email }}
            </div>
            <v-avatar size="50" class="stokeImgMe">
              <v-img :src="this.profile.profilepicture"> </v-img>
            </v-avatar>
          </v-row>
        </v-col>
        <v-col class="pa-0 ma-0" v-if="$vuetify.breakpoint.name === 'xs'">
          <v-row justify="end" class="px-3 py-3">
            <div style="font-size: 17px">
              {{ this.profile.email }}
            </div>
          </v-row>
        </v-col>
      </v-row>
    </v-app-bar>
    <v-bottom-navigation
      app
      absolute
      color="primary"
      grow
      class="fix-bottom"
      v-if="$vuetify.breakpoint.name === 'xs'"
    >
      <v-btn v-for="(m, i) in menu" :key="i" @click="goPage(m.path, i)">
        <span :class="active == i ? 'primary--text' : 'greyC--text'">{{
          m.title
        }}</span>
        <span
          class="icon"
          :class="active == i ? 'icon-active ' + m.icon : m.icon"
        ></span>
      </v-btn>
    </v-bottom-navigation>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      tab: 0,
      profile: "",
      place: "",
      menu: [
        {
          title: "Dashboard",
          path: "/",
        },
        {
          title: "User management",
          path: { name: "usermanagement" },
        },
      ],
      xsClass: "pl-0",
      otherClass: "pl-10",
    };
  },
  methods: {
    async get_profile() {
      await this.axios
        .get(this.$store.state.path_api + "profile", {
          headers: this.$store.state.path_header,
        })
        .then((result) => {
          this.profile = result.data.result.profile.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    goPage(path, i) {
      localStorage.setItem("active", i);
      this.$router.push(path).catch(() => {});
    },
    async get_place() {
      await this.axios
        .get(this.$store.state.path_api + "stat", {
          headers: this.$store.state.path_header,
        })
        .then(async (result) => {
          this.place = result.data.result[0].place;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  async mounted() {
    this.$store.commit("setactive", parseInt(localStorage.getItem("active")));
    if (this.active === 1) {
      this.$router.push("/usermanagement");
    } else if (this.active === 0) {
      this.$router.push("/mobile");
    }
    await this.get_place();
    await this.get_profile();
    this.$store.commit("setloading", false);
  },
  computed: {
    height() {
      if (this.$vuetify.breakpoint.name == "xs") {
        return 90;
      }
      return 70;
    },
    breakpoint() {
      return this.$vuetify.breakpoint.name;
    },
    active() {
      return localStorage.getItem("active");
    },
  },
  watch: {
    tab(newValue) {
      localStorage.setItem("tab", newValue);
    },
  },
};
</script>

<style lang="scss" scoped>
.stokeImgMe {
  border: 2px solid #fff;
}
.fix-bottom {
  position: fixed !important;
  left: 0;
  bottom: 0;
  width: 100%;
}
</style>