<template>
  <div>
    <v-container grid-list-xs v-if="breakpoint === 'xs' || breakpoint === 'sm'">
      <v-card elevation="2" class="ma-2" outlined>
        <v-container grid-list-xs>
          <v-row>
            <v-col>
              <v-container grid-list-xs>
                <v-row justify="end" align="start" class="pl-5">
                  <div class="font-weight-black" style="font-size: 20px">
                    Access Person :
                  </div>
                </v-row>
                <v-row align="end" class="px-3">
                  <div style="font-size: 15px; color: #777777">
                    {{ today }}
                  </div>
                  <v-spacer></v-spacer>
                  <div
                    class="font-weight-black"
                    style="font-size: 50px; color: #ffa62b"
                  >
                    {{ mainStatus_list[0].total_case }}
                  </div>
                </v-row>
              </v-container>
              <v-row justify="center" class="ma-2">
                <v-divider class="ma-0"></v-divider>
              </v-row>
              <v-row class="mx-1">
                <v-col cols="7" class="px-0">
                  <apexchart
                    class="ml-n8"
                    width="215"
                    type="donut"
                    :options="options"
                    :series="series"
                  ></apexchart>
                </v-col>
                <v-col cols="5" align-self="center">
                  <v-row justify="start">
                    <div class="font-weight-black negcovid--text">Green :</div>
                  </v-row>
                  <v-row justify="end">
                    {{ this.mainStatus_list[0].green_case }} ({{
                      this.mainStatus_list[0].g_percent
                    }}%)</v-row
                  >
                  <v-row justify="start">
                    <div class="font-weight-black pluscovid--text">Red :</div>
                  </v-row>
                  <v-row justify="end">
                    {{ this.mainStatus_list[0].red_case }} ({{
                      this.mainStatus_list[0].r_percent
                    }}%)
                  </v-row>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
      <v-card outlined>
        <v-tabs
          v-if="
            $vuetify.breakpoint.name == 'xs' || $vuetify.breakpoint.name == 'sm'
          "
          fixed-tabs
          color="primary"
          height="40px"
          v-model="tab"
          centered
        >
          <v-tab>
            <span>Summary</span>
          </v-tab>
          <v-tab>
            <span>History</span>
          </v-tab>
        </v-tabs>
        <Summary v-if="tab === 0" />
        <History v-if="tab === 1" />
      </v-card>
    </v-container>

    <v-container
      fluid
      grid-list-xs
      class="px-7 py-7"
      v-if="breakpoint === 'md' || breakpoint === 'lg'"
    >
      <v-row>
        <v-col cols="5">
          <v-row class="d-flex align-center justify-center">
            <v-container grid-list-xs>
              <v-card height="190">
                <v-row class="d-flex align-center justify-start pl-5">
                  <v-card-title style="font-size: 35px">
                    Access Person :</v-card-title
                  >
                </v-row>
                <v-row class="d-flex align-center justify-center">
                  <div
                    class="font-weight-black"
                    style="font-size: 80px; color: #ffa62b"
                  >
                    {{ this.mainStatus_list[0].total_case }}
                  </div>
                </v-row>
              </v-card>
            </v-container>
          </v-row>
        </v-col>
        <v-col cols="7">
          <v-row>
            <v-container grid-list-xs>
              <v-card height="190">
                <v-row class="mx-0 px-0" justify="end">
                  <v-col cols="4" align-self="center">
                    <v-row justify="center">
                      <div
                        class="font-weight-black negcovid--text"
                        style="font-size: 30px"
                      >
                        Green
                      </div>
                    </v-row>
                    <v-row justify="center">
                      <div
                        style="font-size: 20px; font-weight: bold"
                        class="negcovid--text"
                      >
                        {{ this.mainStatus_list[0].green_case }}
                      </div>
                    </v-row>
                    <v-row justify="center">
                      <div
                        style="font-size: 20px; font-weight: bold"
                        class="negcovid--text"
                      >
                        ({{ this.mainStatus_list[0].g_percent }}%)
                      </div>
                    </v-row>
                  </v-col>
                  <v-col class="d-flex justify-center align-center">
                    <v-divider class="test" light vertical inset></v-divider>
                  </v-col>

                  <v-col cols="4" align-self="center">
                    <v-row justify="center">
                      <div
                        class="font-weight-black pluscovid--text"
                        style="font-size: 30px"
                      >
                        Red
                      </div>
                    </v-row>
                    <v-row justify="center">
                      <div
                        style="font-size: 20px; font-weight: bold"
                        class="pluscovid--text"
                      >
                        {{ this.mainStatus_list[0].red_case }}
                      </div>
                    </v-row>
                    <v-row justify="center">
                      <div
                        style="font-size: 20px; font-weight: bold"
                        class="pluscovid--text"
                      >
                        ({{ this.mainStatus_list[0].r_percent }}%)
                      </div>
                    </v-row>
                  </v-col>
                  <v-col cols="3" class="px-0 mx-0">
                    <v-row justify="end" class="px-0 mx-0">
                      <apexchart
                        class="ml-n8"
                        width="250"
                        type="donut"
                        :options="options"
                        :series="series"
                      ></apexchart>
                    </v-row>
                  </v-col>
                </v-row>
              </v-card>
            </v-container>
          </v-row>
        </v-col>
      </v-row>
      <v-row>
        <v-col md="4">
          <v-card height="500">
            <History />
          </v-card>
        </v-col>
        <v-col md="8">
          <v-card height="500">
            <Summary />
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import Summary from "../components/summary.vue";
import History from "../components/history.vue";
export default {
  components: {
    Summary,
    History,
  },
  data() {
    return {
      tab: 0,
      options: {
        chart: {
          type: "donut",
        },
        noData: {
          text: "Loading...",
        },
      },
      series: [],
      mainStatus_list: [],
      today: Date().toString().slice(0, 15),
    };
  },
  methods: {
    async get_main_data() {
      await this.axios
        .get(this.$store.state.path_api + "stat", {
          headers: this.$store.state.path_header,
        })
        .then(async (result) => {
          for (const item of result.data.result) {
            this.mainStatus_list.push(item);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    async updateChart() {
      this.options = {
        labels: ["Green", "Red"],
        fill: {
          type: "gradient",
          gradient: {
            shade: "light",
            type: "horizontal",
            shadeIntensity: 0.2,
            inverseColors: false,
          },
        },
        dataLabels: {
          enabled: false,
        },
        legend: {
          show: false,
        },
        colors: [
          this.$vuetify.theme.themes.light.negcovid,
          this.$vuetify.theme.themes.light.pluscovid,
        ],
      };
      this.series = [
        this.mainStatus_list[0].green_case,
        this.mainStatus_list[0].red_case,
      ];
    },
  },
  async mounted() {
    await this.get_main_data();
    await this.updateChart();
    this.$store.commit("setloading", false);
  },
  computed: {
    breakpoint() {
      return this.$vuetify.breakpoint.name;
    },
  },
};
</script>

<style scoped>
.test {
  width: 100% !important;
}
</style>
